.App {
  text-align: center;
  margin: 0px;
  /* width: 100vw; */
  padding: 0px;
  background: var(--homebackgraund);
/* border: 1px solid red; */
background: var(--appcssback);
}
::-webkit-scrollbar{
  width: 10px;
  /* border: 1px solid rgb(255, 255, 255); */
}
::-webkit-scrollbar-thumb{
  border-radius: 10px;
  border: none;
  background:linear-gradient(transparent, #121216 80%);
}
::-webkit-scrollbar-thumb:hover{
  background:linear-gradient(transparent, #817783 80%);
}
.title_div{
  color: var(--titletext);
  text-align: center;
  padding: 2rem ;
  padding: 5rem 0rem 3rem 0rem;
}
.title_div>:nth-child(1){
  font-size:4rem;
  color: var(--LineTitle);

}
.title_div>:nth-child(2){
  font-size:1.5rem;
  margin-bottom: 3rem;
  color: var(--homemyname);
  
}
.light_theam{

  
  /* home */


  --gradient:black;
  --basictext:rgb(175, 49, 49);
  --appcssback:white;
  --backgraundshadow: #1d3873;
 
  --backgraundcolor:#dadada;
  --homegradient:radial-gradient(circle, rgba(2,0,36,1) 65%, rgba(28,28,177,0.9920343137254902) 97%, rgba(4,187,244,1) 100%, rgba(0,212,255,1) 100%);
}
.dark_theam{

  /* --homebackgraund: linear-gradient(to right, #434343 0%, black 100%); */
