  --homebackgraund: linear-gradient(to right, #434242 0%, black 100%);
--homesubtext:white;
--homemyname:#CCCCFF;
--var:#F0F8FF;
--basicdarktext:black;
--LineTitle: #52c5ff;

  /* --homegradient:#cdcdcd;
  --aboutgradent:#d2d2e4; */




--primarybackgraund:#6F00FF;
--textboxbackgraund:#7B68EE;
--overtext:rgb(255, 255, 255);
