
.Techstack {
  
    gap: 5rem;
    height: auto;
    padding: 4.5rem 2.5rem;
    width: 100%;
    background: var(--homebackgraund);
  }
  .techstack_div{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  width: 80%;
 gap: 40px;
  margin: auto;
  /* border: 1px solid black; */
  }
  .Touterdiv{
    padding:2.5rem 3rem 2.5px 3rem;
    border-radius: 2rem;
    background: var(--homemyname);
    box-shadow: rgb(106, 93, 93) 0px 1px 2px, rgb(255, 255, 255) 0px 2px 4px, rgba(167, 7, 241, 0.07) 0px 4px 8px, rgba(0, 0, 0, 0.07) 0px 8px 16px, rgba(0, 0, 0, 0.07) 0px 16px 32px, rgba(226, 225, 228, 0.07) 0px 32px 64px;
  /* border: 1px solid  red; */
  }
  .Touterdiv:hover{
    transition: ease-in-out 10ms;
    transform: scale(1.1);
  }
  
  .Touterdiv>:nth-child(1){
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
  }
    .Touterdiv>:nth-child(2){
    font-size: 1.5rem;
    text-align: center;
    margin-top: 1.2rem;
    color: var(--basicdarktext);
  }

  .Timgs{
  width: 7rem;
  margin: auto;
  box-shadow: rgba(255, 255, 255, 0.2) 0px 7px 29px 0px;
  border-radius: 20%;
  padding: 2px;
  display: flex;
  justify-content: center;
  }
  
  
  
  .Subtitle{
    text-align: center;
    font-size: 1.8rem;
    color: var(--basicdarktext); 
    background: var(--homesubtext);
    width: 20%;
    border: 1px solid black;
    margin: auto;
    padding: 1rem;
    margin-top:3rem;
    margin-bottom: 3rem;
    border-radius: 2rem;
  }
  .title{
    /* margin: 5rem 0rem 0.5rem 0rem; */
    color: var(--titletext);
    text-decoration: underline;
   
  }
  

  
  @media (min-width: 390px) and (max-width: 750px) {
   
    .Techstack {
      gap: 1rem;
      margin-bottom: 4rem;
      padding: 0.1rem 0.1rem;
    }


    .techstack_div{
     row-gap: 1rem;
     column-gap: 1rem;
    }
    
     
      .Touterdiv{
        padding:1.5rem 2rem 1.5rem 2rem;
        box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
      }
      .Touterdiv>:nth-child(2){
        font-size: 1rem;
      }


      .Subtitle{
        text-align: center;
        font-size: 1.4rem;
        background-color: rgb(239, 221, 221);
        width: 40%;
      }
      .Timgs{
        width: 50px;
        }
        
  }
  
  
  
