#About{
    background: var(--homebackgraund);
    /* height: 100vh; */
   padding-bottom: 4rem;
}


.compotitile{
    text-align: start;
    color: red;
}
.contentdiv{
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    align-items: center;
}
.contentdiv>img{
    width: 40%;
    height: 70vh;
border-radius: 10%;
}
.contentdiv>div{
    width: 50%;
    /* background-colo/r: var(--textboxbackgraund); */
    /* border: 1px solid red; */
    text-align: start;
    padding: 2.2rem;
    background:var(--homebackgraund);
    border-radius: 10%;
/* background: linear-gradient(19deg, #21D4FD 0%, #B721FF 100%); */

}

.aboutmassage{
    line-height: 2.2rem;
    word-spacing: 0.7rem;
    font-family: sans-serif;
    color: var(--homemyname);
}


@media (min-width: 390px) and (max-width: 767px) {
    #About{
        height: 95vh;
        /* border: 1px solid black; */
        /* margin-top: 4rem; */
    }
   
    .contentdiv{
        display: flex;
        width: 90%;
        height: 70vh;
        /* border: 1px solid black; */
        margin: auto;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .contentdiv>img{
      display: none;
    }
    .contentdiv>div{
        width: 95%;
        text-align: center;
        padding: 2.2rem;
        height: 70vh;
    }
    .aboutmassage{
        line-height: 1.5rem;
        word-spacing: 0.7rem;
        font-size: 1.5rem;
        
    }
}