.contact-form1 {
  width: 100%;
background: var(--homebackgraund);
padding-bottom: 4rem;
}

.contact_data {
    justify-content: center;
    display: flex;
    align-items: center; 
    box-shadow:var(--homesubtext) 0px 3px 8px;
    width: 70%;
    padding: 1rem 4rem;
    /* height: 90vh; */
    margin: auto;
    /* margin-bottom: 100px; */
  }
.followme{
    /* border: 1px solid black; */
    width: 35%;
    margin: auto;
    /* height: 40vh; */
}

.icondiv{
    display: flex;
    box-shadow:var(--homesubtext) 0px 1px 3px 0px, rgba(240, 244, 248, 0.15) 0px 0px 0px 1px;
    /* box-shadow:var(--homesubtext)0px 1px 4px; */
    align-items: center;
    padding: 10px;
    margin-top: 10px;
    width: 100%;
}
.icondiv>:nth-child(1){
    width: 40px;
    color: var(--LineTitle);
    /* background:var(--primarybackgraund); */
    height: 50px;
}
.icondiv>:nth-child(2){
  text-align: start;
    margin-left: 2rem;
    font-size: 16px;
    color: var(--homesubtext);
    text-decoration: none;
    
    /* border: 1px solid red; */
}
.formdiv{
    /* border: 1px solid rgb(41, 255, 17); */
    width: 50%;
    display: flex;
    height: 60vh;
    margin: auto;
justify-content: center;

}

.formdiv>form{
margin-top: 50px;
    /* border: 1px solid red; */
    text-align: center;

}
.user {
  width: 90%;
  height: 4rem;
  margin: 10px;
  padding: 0.3em;
  font-size: 16px;
  background: transparent;
  border-radius: 8px;
  border: 1px solid var(--LineTitle);

}
.user:focus{
  border: 1px solid black;
}
.button1 {
  width: 40%;
  margin: auto;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 5%;
  background: transparent;
  border: none;
  box-shadow: var(--overtext) 0px 3px 8px;
}
.button1:hover{
 background-color:  var(--LineTitle);
 color: white;
}
textarea {
  height: 10rem !important;
}

@media (min-width: 390px) and (max-width: 750px) 
  {
    .followme{
     display: none;
  }
  .contact_data {
    width: 75%;
    padding: 1rem 2rem;
    border-radius: 10%;
  }
  .formdiv{
    /* border: 1px solid rgb(41, 255, 17); */
    width: 100%;
    display: flex;
    height: 70vh;
    margin: auto;
justify-content: center;

}

.formdiv>form{
margin-top: 50px;
    /* border: 1px solid red; */
    text-align: center;

}

  


}
